.legion-modal{width:100%;height:100%;position:fixed;z-index:500;top:0;left:0;background-color:rgba(0,0,0,.6);color:#fff;display:grid;justify-content:center;align-content:center}.legion-modal-inner{width:408px;height:434px;border:1px solid #000;background:url("../../images/water_texture.jpg") no-repeat center;background-size:cover;box-shadow:0px 2px 2px 10px rgba(0,0,0,.75);background-size:cover;background-repeat:no-repeat;display:flex;flex-direction:column}.close-button{float:right}.hidden{width:2048px;height:1522px;display:none}.add-legion{padding:10px;background-color:rgba(79,44,44,.8)}.add-legion div,.add-legion button{color:#e8a506}.add-legion button{width:100%;margin-top:10px;border-color:#4f2c2c;box-shadow:0px 2px 2px 2px rgba(0,0,0,.75)}.input-row{margin:2px 0}.input-row span{width:100px;display:inline-block;text-align:right;padding-right:5px}.bottom-area{height:100%;position:relative}.error{min-height:20px;width:100%;text-align:center;margin:15px 0 0 0;font-size:1.3em;font-style:italic;color:red}.preview-image{display:grid;justify-content:center;align-content:center;width:100%;height:100%;position:absolute;top:0px;left:0px}.army-icon{width:50px;height:50px;border:none;box-shadow:0px 0px 30px 5px #000;opacity:1}.disabled{background-color:#999898}