.interactive-map-wrapper{height:370px;width:1024px;position:relative;border:2px solid #000}.map-layer{background-color:#345155;background:url("../../images/water_texture.jpg") no-repeat center;background-size:cover;position:absolute;top:0;bottom:0;width:100%}div.full-screen{position:fixed;top:0;left:0;z-index:10;width:100vw;height:100%}.full-screen-btn{position:absolute;top:0px;right:0px;z-index:1000;background-color:#4f2c2c;color:#e8a506;width:25px;height:25px;margin:5px;cursor:pointer}.full-screen-btn:hover{background-color:#6b3c3c}.full-screen-btn-fullscreen{width:40px;height:40px}.map-image{border:none;margin:0px;padding:0px}